// main settings
$default-font: "RobotoLight", sans-serif;                                    // Main font
$bgcolor:         #292929;                                                     // body bg color
$text:            rgba(255,255,255,.87);                                                        // text color
$main_color:      #aaa;                                                        // main color
$accent_color:    #fff;                                                        // accent color
$container-width: 1140px;                                                      // container width

// menu settings
$burger-color: #fff;                                                           // burger color

/* modal settings */
$modalwidth: 600px;
$modalbg: #fff;

// input, textarea settings
$input-placeholder: #ddd;                                                      // placeholder color
$input-border-color: #aaa;                                                     // input border color
$input-padding: 10px 15px;                                                     // input padding
$input-border-radius: 5px;                                                     // input border radius
$input-border-error: #ff0000;                                                  // error input border color

// magnific-popup settings (/libs/magnific-popup/src/css/_settings.scss)
$mfp-overlay-color:     #000;                                                  // Color of overlay screen
$mfp-overlay-opacity:   0.5;                                                   // Opacity of overlay screen

$close-btn-size: 44px;                                                         // close btn size
$close-btn-image: false;                                                       // close btn image true/false
$close-btn-color: #fff;                                                        // close btn color
$close-btn-link: '../images/close-icon.png';                                   // close btn image

